import domain from "../domain";
import backEndApiList from "@/Config_Backend_API";

export default {
    methods: {
        submitPaymentsPlaningDataList: async function(obj) {
            let response = await this.axios.post(
                domain.APP_DOMAIN + backEndApiList.PaymentsPlaning.Create_Payments_Planing,
                obj,
                domain.APP_HEADER
            );
            return response.data;
        },

        submitPaymentsPlaningDataListSaveToAccount: async function(obj) {
            let response = await this.axios.post(
                domain.APP_DOMAIN + backEndApiList.PaymentsPlaning.Create_Payments_Planing_To_Account,
                obj,
                domain.APP_HEADER
            );
            return response.data;
        },

        getPaymentsPlaningDataListByQueryParam: async function(str) {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.PaymentsPlaning.Get_Payments_Planing_By_Query_Param + str,
                domain.APP_HEADER
            );
            return response.data;
        },

        getPaymentsPlaningChargeListByQueryParam: async function(str) {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.PaymentsPlaning.Get_Payments_Planing_Charge_List_By_Query_Param + str,
                domain.APP_HEADER
            );
            return response.data;
        },

        getAddPaymentsDataList: async function() {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.PaymentsPlaning.Get_Add_Payments_Details_All,
                domain.APP_HEADER
            );
            return response.data;
        },

        getPaymentsPlaningDataList: async function() {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.PaymentsPlaning.Get_Payments_Planing_All,
                domain.APP_HEADER
            );
            return response.data;
        },

        deletePaymentsPlaningItemById: async function(id) {
            let response = await this.axios.delete(
                domain.APP_DOMAIN + backEndApiList.PaymentsPlaning.Delete_Payments_Planing_Item_By_Id + id,
                domain.APP_HEADER
            );
            return response.data;
        }
    }
}