import domain from "../domain";
import backEndApiList from "@/Config_Backend_API";

export default {
    methods: {
        getInquiryListBySearch: async function(obj) {
            let response = await this.axios.post(
                domain.APP_DOMAIN + backEndApiList.Inquiry.Get_Inquiry_List_By_Search,
                obj,
                domain.APP_HEADER
            );
            return response.data;
        },

        getInquiryInvoiceById: async function(id) {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.Inquiry.Get_Inquiry_Invoice_By_Id + id,
                domain.APP_HEADER
            );
            return response.data;
        },

        updateInquiryInvoiceByID: async function(id, obj) {
            let response = await this.axios.put(
                domain.APP_DOMAIN + backEndApiList.Inquiry.Update_Inquiry_Invoice_By_ID + id,
                obj,
                domain.APP_HEADER
            );
            return response.data;
        }
    }
}