import domain from "../domain";
import backEndApiList from "@/Config_Backend_API";

export default {
    methods: {
        createPreShipment: async function(obj) {
            let response = await this.axios.post(
                domain.APP_DOMAIN + backEndApiList.Pre_Shipment.Create_Pre_Shipment,
                obj,
                domain.APP_HEADER
            );
            return response;
        },

        getAllPreShipments: async function() {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.Pre_Shipment.Get_All_Pre_Shipments,
                domain.APP_HEADER
            );
            return response.data;
        },

        getPreShipmentsByQueryParam: async function(str) {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.Pre_Shipment.Get_Pre_Shipments_By_Query_Param + str,
                domain.APP_HEADER
            );
            return response.data;
        },

        getPreShipmentById: async function(id) {
            let response = await this.axios.get(
                domain.APP_DOMAIN + backEndApiList.Pre_Shipment.Get_Pre_Shipment_By_Id + id,
                domain.APP_HEADER
            );
            return response.data;
        },

        updatePreShipmentByID: async function(id, obj) {
            let response = await this.axios.put(
                domain.APP_DOMAIN + backEndApiList.Pre_Shipment.Update_Pre_Shipment_By_Id + id,
                obj,
                domain.APP_HEADER
            );
            return response.data;
        },

        deletePreShipmentById: async function(id) {
            let response = await this.axios.delete(
                domain.APP_DOMAIN + backEndApiList.Pre_Shipment.Delete_Pre_Shipment_By_Id + id,
                domain.APP_HEADER
            );
            return response.data;
        }
    }
}