<script>
  import Dashboard from '../Export Department/Dashboard'
  import CargoAdvice from '../Export Department/Transactions/Cargo Advice/CargoAdvice'
  import Preshipment from '../Export Department/Transactions/Pre-Shipment/Preshipment'
  import PackingPreShipment from '../Export Department/Transactions/Packing Pre-shipment/PackingPreShipment'
  import ShipmentPlaning from '../Export Department/Transactions/ShipmentPlaning/ShipmentPlaning'
  import LCDetails from '../Export Department/Transactions/LC Details/LCDetails'
  import performaInvoice from '../Export Department/Transactions/Performa Invoice/performaInvoice'
  import Inquiry from '../Export Department/Inquiry'
  import PaymentsPlanning from '../Export Department/PaymentsPlanning'

  const routes = [
    { path: '*', redirect: '/dashboard' },
    { path: '/dashboard', component: Dashboard },
    { path: '/cargo-advice/:mode', name: "cargoAdvice", component: CargoAdvice },
    { path: '/preshipment/:mode', name: "preShipment", component: Preshipment },
    { path: '/packing-pre-shipment/:mode', name: "packingPreShipment", component: PackingPreShipment },
    { path: '/shipment-planing', component: ShipmentPlaning },
    { path: '/lc_details/:mode', name: "lcDetails", component: LCDetails },
    { path: '/performa-invoice/:mode', name: "performaInvoice", component: performaInvoice },
    { path: '/inquiry', name: "inquiry", component: Inquiry },
    { path: '/shipment-charge', name: "paymentsPlanning", component: PaymentsPlanning }
  ]

  export default routes;
</script>
