<template>
    <div>
        <div class="mailbox-list-item" v-for="(invoice, index) in invoiceDetails" v-bind:class="{ ' unread': !invoice.read, 'has-attachment': invoice.attachment }" v-bind:key="index" :style="selectedIndex === index ? 'background-color: #f6f7fa;' : 'background-color: #fff;'" @click="handleClickedEvent(invoice, index)">
            <div class="mailbox-checkbox">
                <div class="custom-control custom-checkbox">
                    <input :disabled="showCompInInquiryMode" class="custom-control-input" type="checkbox" v-model="invoice.checked" v-bind:id="'mailCheckbox' + index" @change="handleCheckBoxChangeEventInList(invoice.checked, index)"/>
                    <label class="custom-control-label" v-bind:for="'mailCheckbox' + index"></label>
                </div>
            </div>
            <div class="mailbox-message">
                <router-link to="" class="mailbox-list-item-link">
                    <div class="mailbox-sender">
                        <span class="mailbox-sender-name" v-if="!_routerRoot._route.path.includes('/packing-pre-shipment') && !_routerRoot._route.path.includes('/performa-invoice')">{{ invoice.sender }}</span>
                        <span class="mailbox-sender-name" v-if="_routerRoot._route.path.includes('/performa-invoice') || _routerRoot._route.path.includes('/packing-pre-shipment')">{{ invoice.inv_number }}</span>
                        <!-- <span class="mailbox-sender-name" v-if="_routerRoot._route.path.includes('/performa-invoice')">{{ invoice.inv_number }}</span> -->
                        <span class="mailbox-time">{{ invoice.date }}</span>
                    </div>
                    <div class="mailbox-title">{{ invoice.title }}</div>
                    <div class="mailbox-title" v-if="filterCondition === 'Color'">Color Code : {{ invoice.color }}</div>
                    <div class="mailbox-title" v-if="filterCondition === 'Style'">Style Code : {{ invoice.style }}</div>
                    <div class="mailbox-desc">{{ invoice.content }}</div>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        invoiceDetails: {
            type: Array,
            default: null
        },
        showCompInInquiryMode: {
            type: Boolean,
            default: false
        },
        selectedIndex: {
            type: Number,
            default: 0
        },
        filterCondition: {
            type: String,
            default: null
        }
    },
    watch: {
        invoiceDetails: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        showCompInInquiryMode: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        selectedIndex: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        filterCondition: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        }
    },
    data() {
		return {}
	},
	methods: {
        handleClickedEvent(value, index) {
            this.$emit("handleClickedEvent", value, index);
        }, 
        handleCheckBoxChangeEventInList(value, index) {
            this.$emit("handleCheckBoxChangeEventInList", value, index);
        }
    }
}
</script>

<style lang="scss" scoped>
    .mailbox .mailbox-list .mailbox-message .mailbox-title {
        font-weight: 600;
        color: #212837;
    }    
</style>