<template>
    <div>
    <!-- ---------------------- Inquiry ---------------------- -->
        <b-col v-if="mode === 'shipment_planning_table'">
            <b-row>
                <div class="table-responsive resShipmentTabel">
                    <div style="border: 1px solid #bababa; border-radius: 5px; margin-top: 10px;">
                        <div class="mainColumn" v-for="(invoice, index) in invoiceDetails" v-bind:key="index">
                            <b-row>
                                <b-col sm="12" md="3" lg="3" style="font-size: 16px;" v-if="selectType === 'Cargo Advice' || selectType === 'Lc Details' || selectType === 'Pre Shipment'">
                                    <span class="docNo">Document Number : {{ invoice.sender }}</span>
                                </b-col>
                                <b-col sm="12" md="3" lg="3" style="font-size: 16px;" v-if="selectType === 'Packing Pre Shipment' || selectType === 'Performa Invoice'">
                                    <span class="docNo">Document Number : {{ invoice.inv_number }}</span>
                                </b-col>
                                <!-- <b-col sm="12" md="2" lg="2" style="font-size: 16px;" v-if="selectType === 'Cargo Advice' || selectType === 'Performa Invoice'">
                                    {{ invoice.buyerName }}
                                </b-col> -->
                                <b-col sm="12" md="3" lg="3" style="font-size: 16px;" v-if="selectType === 'Cargo Advice' || selectType === 'Lc Details' || selectType === 'Performa Invoice'">
                                    {{ invoice.title }}
                                </b-col>
                                <b-col sm="12" md="3" lg="3" style="font-size: 16px;" v-if="selectType === 'Packing Pre Shipment'">
                                    Order No : {{ invoice.customerOrderNo }}
                                </b-col>
                                <b-col sm="12" md="3" lg="3" style="font-size: 16px;" v-if="selectType === 'Pre Shipment'">
                                    Order No : {{ invoice.customerOrderNumber }}
                                </b-col>
                                <b-col sm="12" md="2" lg="2" style="font-size: 16px;">
                                    {{ invoice.date }}
                                </b-col>
                                <b-col sm="12" md="2" lg="2" style="display: flex; justify-content: center;">
                                    <b-button @click="handleRedirectToView('update', selectType)" variant="outline-primary" class="px-5 mr-3">
                                        Update
                                    </b-button>
                                    <b-button @click="handleRedirectToView('inquiry', selectType)" variant="outline-primary" class="px-5">
                                        View
                                    </b-button>
                                </b-col>
                            </b-row>
                        </div>
                    </div>
                </div>
            </b-row>
        </b-col>
    </div>
</template>

<script>

export default {
    props: {
        selectType: {
            type: String,
            default: null
        },
        fieldList: {
            type: Array,
            default: null
        },
        invoiceDetails: {
            type: Array,
            default: null
        },
        selectedIndex: {
            type: Number,
            default: 0
        },
        filterCondition: {
            type: String,
            default: null
        },
        mode: {
            type: String,
            default: null
        },
        search_text: {
            type: String,
            default: null
        }
    },
    watch: {
        selectType: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        fieldList: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        invoiceDetails: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        selectedIndex: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        filterCondition: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
        search_text: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        },
    },
    data() {
		return {
            selected: []
        }
    },
    methods: {
        handleRedirectToView(value, type) {
            let val = "";
            val = type === "Cargo Advice" ? "cargoAdvice" : type === "Lc Details" ? "lcDetails" : type === "Packing Pre Shipment" ? "packingPreShipment" : type === "Pre Shipment" ? "preShipment" : "performaInvoice";
            this.$router.push({
                name: val,
                params: { mode: value, search: this.search_text } // -------- set url params -------
            });
        }
    }
}
</script>

<style lang="scss" scoped>
    .shipmentTbleData{
        padding: 10px;
        min-width: 150px;
        border-right: 1px solid #a8b6d1;
        border-bottom: 1px solid #a8b6d1;
    }
    .resShipmentTabel {
        border: 0px solid #a8b6d1;
        margin-top: -15px;
    }
    .docNo {
        font-weight: bold;
        color: #6d6a6a;
    }
    .mainColumn {
        background: #ffffff;
        border-bottom: 1px solid #bababa;
        padding: 15px;
    }
    .mainColumn:hover {
        background: #eeeeee;
    }
</style>