<template>
    <div>
  <!-- ---------------------- Shipment Planning---------------------- -->
<!-- phase 01  -->
        <div class="px-1 py-2" v-if="mode === 'modal-center-shipmentPlaning-phase01'">
            <b-row class="mt-3">
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Ex Factory Date"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input type="date" v-model="shipmentPlanningPhase01.exFactoryDate"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Booking Date"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input type="date" v-model="shipmentPlanningPhase01.bookingDate" ></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
            </b-row>
            <b-row>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Revised Booking Date"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input type="date" v-model="shipmentPlanningPhase01.revisedBookingDate"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Booking No"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input placeholder="23525" type="number" v-model="shipmentPlanningPhase01.bookingNo"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
            </b-row>
            <b-row>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Cusdec No"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input placeholder="4535435" type="number" v-model="shipmentPlanningPhase01.cusdecNo"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
            </b-row>
            <b-row>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Week No"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input placeholder="13" type="number" v-model="shipmentPlanningPhase01.weekNo"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Plan No"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input placeholder="12" type="number" v-model="shipmentPlanningPhase01.planNo"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
            </b-row>
            <b-row>
                <div class="col-sm-6 ml-3 mt-3">
                    <b-button @click="handlePhase01ShipmentPlanning()" variant="primary px-5">Update</b-button>
                </div>
            </b-row>
        </div>
<!-- phase 02  -->
        <div class="px-3 py-2" v-if="mode === 'modal-center-shipmentPlaning-phase02'" >
            <b-row>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Ex Factory Date Cargo Pick Up"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input type="date" v-model="shipmentPlanningPhase02.cargoPickup"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Cargo Cutoff Date and Time"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input type="date" v-model="shipmentPlanningPhase02.cutoffDate"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
            </b-row>
            <b-row>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Logistic Cutoff Date and Time"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input type="date" v-model="shipmentPlanningPhase02.LogisticCutoffDate"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
                <div class="col-sm-6">
                    <b-form-group class="mt-1 mb-3 ml-3" style="color:black" id="fieldset-horizontal" label="Cargo Ready to CFS/Port Date and Time"  label-for="input-horizontal" >
                        <b-input-group>
                            <b-form-input type="date" v-model="shipmentPlanningPhase02.cargoReadyDate"></b-form-input>
                        </b-input-group>
                    </b-form-group>
                </div>
            </b-row>
            <b-row >
                <div class="col-sm-6 ml-3 mt-3">
                    <b-button  @click="handlePhase02ShipmentPlanning()" variant="primary px-5">Update</b-button>
                </div>
            </b-row>
        </div>
    </div>
</template>

<script>
import APIShipmentPlanning from "../../mixins/Shipment Planning/APIShipmentPlanning";

export default {
    props: {
        mode: {
            type: String,
            default: null
        },
        selectedTableRows: {
            type: Array,
        },
        invoiceDetails: {
            type: Array,
            default: null
        },
    },
    watch: {
        mode: {
            handler(val) {
                if (val) {
                    return val;
                }
            },
            immediate: true,
            deep: true
        }
    },
    data() {
		return {
            IDsFromRowSelected: [],
            shipmentPlanningPhase01: {
                planNo:"",
                weekNo:"",
                cusdecNo:"",
                bookingNo:"",
                revisedBookingDate:"",
                bookingDate:"",
                exFactoryDate:"",
            },
            shipmentPlanningPhase02: {
                cargoPickup:"",
                cutoffDate:"",
                LogisticCutoffDate:"",
                cargoReadyDate:"",
            },
            newDataList:[],
            allDataArray:[]
        }
    },
    created: {},
    mixins: [APIShipmentPlanning],
    methods: {
        handlePhase01ShipmentPlanning: async function() {
			try {
                let self = this;
                self.selectedTableRows.map(invoice => {
                
                    invoice.bookingNo = this.shipmentPlanningPhase01.bookingNo;
                    invoice.CusdecNo = this.shipmentPlanningPhase01.cusdecNo;
                    invoice.revisedBokkingDate = this.shipmentPlanningPhase01.revisedBookingDate;
                    invoice.bookingDate = this.shipmentPlanningPhase01.bookingDate;
                    invoice.exFactoryDate = this.shipmentPlanningPhase01.exFactoryDate;
                    invoice.weekNo = this.shipmentPlanningPhase01.weekNo;
                    invoice.planNo = this.shipmentPlanningPhase01.planNo;
                    self.newDataList.push(invoice)
                    // self.allDataArray = self.newDataList.concat(this.invoiceDetails);

                });
				await this.updateShipmetPlanningPhase01(this.newDataList);
                
			} catch (err) {
               
                console.log(err);
                this.$bvModal.hide("modal-center-shipmentPlaning-phase01");
                
			}
		},
        handlePhase02ShipmentPlanning: async function() {
			try {
                let self = this;
                self.selectedTableRows.map(invoice => {
                
                    invoice.exFactoryDateCargoPickup = this.shipmentPlanningPhase01.cargoPickup;
                    invoice.cargoCutOffDateforTime = this.shipmentPlanningPhase01.cutoffDate;
                    invoice.logisticCutoffDateTimeDate = this.shipmentPlanningPhase01.LogisticCutoffDate;
                    invoice.cargoRedyDate = this.shipmentPlanningPhase01.cargoReadyDate;                    
                    self.newDataList.push(invoice)
                    // self.allDataArray = self.newDataList.concat(this.invoiceDetails)

                });
				await this.updateShipmetPlanningPhase02(this.newDataList);
			} catch (err) {
				this.$bvToast.toast('Details Submit Success!', {
					title: 'Success',
					noAutoHide: false,
					variant: 'primary',
					appendToast: false
				});
				console.log(err);
                this.$bvModal.hide("modal-center-shipmentPlaning-phase02");
                
			}
		},
    },

}
</script>

