import backEndApiList from "@/Config_Backend_API";

let Domain = Object.freeze({
    path: backEndApiList.Api_Url
});

export const apiDomain = Domain.path;
export default {
    APP_DOMAIN: apiDomain,
    get APP_HEADER() { // ---------------- json format api call ----------------
        const AuthStr = "Bearer ".concat(localStorage.token);
        return {
            headers: {
                Authorization: AuthStr
            }
        };
    },
    get APP_HEADER_FORM_DATA() { // ---------------- form upload api call ----------------
        const AuthStr = "Bearer ".concat(localStorage.token);
        return {
            headers: {
                Authorization: AuthStr,
                "Content-Type": "multipart/form-data"
            }
        };
    }
}