import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    update_top_header_breadcumb: [],
    update_middle_top_header_breadcumb: [],
  },
  mutations: {
    toTopHeaderBreadCumb: (state, value) => {
      state.update_top_header_breadcumb = value;
    },
    toMiddleTopHeaderBreadCumb: (state, value) => {
      state.update_middle_top_header_breadcumb = value;
    }
  },
  getters: {},
  actions: {}
});
